.dark-web-monitoring-page .manage-passowrds .row3{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0;
    padding:0 7.5%;
    .img-cont{
        content: url('../../../../assets/DarkWebMonitoring/screens.png');
        width: 50%;
    }
    .col1{
        width: 30%;
    }
    h1{
        font-size: 35px;
        line-height: 1.5;
        span{
            color:#427cff;
        }
    }
    p{
        font-size: 18px;
        margin:30px 0 40px;
        color:#777;
        display: flex;
        align-items: center;
        svg{
            font-size: 22.5px;
            margin-right: 15px;
        }
    }
}

@media(max-width:1401px){
    .dark-web-monitoring-page .manage-passowrds .row3{
        h1{
            font-size: 30px;
            line-height: 1.5;
        }
        p{
            font-size: 15px;
            margin:25px 0 35px;
            svg{
                font-size: 20px;
                margin-right: 10px;
            }
        }
    }
}

@media(max-width:900px){
    .dark-web-monitoring-page .manage-passowrds .row3{
        .img-cont{
            content: url('../../../../assets/DarkWebMonitoring/screens.png');
            width: 45%;
        }
        .col1{
            width: 45%;
        }
    }
}

@media(max-width:767px){
    .dark-web-monitoring-page .manage-passowrds .row3{
       flex-wrap: wrap;
        margin:20px 0 30px;
        .img-cont{
            width: 400px;
            max-width: 100%;
            margin:15px auto;
        }
        .col1{
            width: 400px;
            max-width: 100%;
            margin:15px auto;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        h1{
            font-size: 20px;
            line-height: 1.3;
            text-align: center;
        }
        p{
            margin:15px 0 20px;
        }
    }
}

//==============================================
.dark-web-monitoring-page .manage-passowrds .left-right{
    padding:0 10%;
    .col1{
        width: 35%;
    }
    .img{
        width: 42.5%;
        border-radius: 30px;
        transition: 0.3s ease-in-out;
        &:hover{
            transform: scale(1.03);
            border-radius: 10%;
        }
    }
}

@media(max-width:767px){
    .dark-web-monitoring-page .manage-passowrds .left-right{
        padding:0 7.5%;
        .col1{
            width: 100%;
            margin:20px auto;
            text-align: center;
            a{
                margin:0 auto;
            }
        }
        .img{
            width: 90%;
            border-radius: 20px;
            margin:20px auto;
        }
    }
}