.dark-web-monitoring-page .features .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:5% 0 5% 7.5%;
    .col1{
        width: 30%;
        h1{
            font-size: 35px;
        }
        p{
            font-size: 19px;
            margin:20px 0;
        }
        a{
            color:#427cff;
            display: flex;
            align-items: center;
            font-size: 18px;
            svg{
                margin-left: 7.5px;
            }
        }
    }
    .col2{
        width: 55%;
        position: relative;
    }
    .bg{
        content: url('../../../../assets/DarkWebMonitoring/bg.png');
        position: absolute;
        width: 70%;
        right: -15%;
        top:-10%;
    }
    .items1{
        width: 77.5%;
        position: relative;
    }
    .item1{
        width: 80%;
        border:1px solid #eee;
        background-color: #fff;
        box-shadow: rgba(50, 50, 105, 0.1) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
        padding:20px 35px;
        display: flex;
        align-items: center;
        border-radius: 10px;
        &:first-child{
            margin:25px 0;
        }
        &:nth-child(2){
            margin:25px auto;
        }
        &:last-child{
            margin: 25px 0 15px auto;
        }
        &:hover{
            background-color: #427cff;
            border:1px solid #427cff;
            .id{
                background-color: #fff;
                color:#427cff;
            }
            h6,p{
                color:#fff;
            }
        }
    }
    .id{
        background-color: #427cff;
        color:#fff;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 20px;
        font-size:40px;
        line-height: 1;
        padding-bottom: 5px;
    }
    .block{
        flex: 1;
        h6{
            font-size: 18px;
            margin-bottom: 5px;
        }
        p{
            font-size: 17px;
        }
    }
}

@media(max-width:1401px){
    .dark-web-monitoring-page .features .row1{
        .col1{
            width: 35%;
            h1{
                font-size: 27.5px;
            }
            p{
                font-size: 16px;
                margin:15px 0;
            }
            a{
                font-size: 15px;
                svg{
                    margin-left: 5px;
                }
            }
        }
        .bg{
            right: -15%;
            top:0;
        }
        .items1{
            width: 80%;
        }
        .item1{
            box-shadow: rgba(50, 50, 105, 0.1) 0px 1px 4px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
            padding:15px 25px;
            &:first-child{
                margin:20px 0;
            }
            &:nth-child(2){
                margin:20px auto;
            }
            &:last-child{
                margin: 20px 0 15px auto;
            }
        }
        .id{
            width: 55px;
            height: 55px;
            margin-right: 15px;
            font-size:30px;
            padding-bottom: 5px;
        }
        .block{
            h6{
                font-size: 15px;
            }
            p{
                font-size: 14px;
            }
        }
    }
}

@media(max-width:900px){
    .dark-web-monitoring-page .features .row1{
       flex-wrap: wrap;
        padding:0 7.5%;
        margin:30px 0;
        .col1{
            width: 100%;
            margin:20px 0;
            h1{
                font-size: 20px;
            }
            p{
                font-size: 15px;
            }
        }
        .col2{
            width: 100%;
            margin:20px 0;
        }
        .bg{
            width: 100%;
            right: unset;
            left: 50%;
            top:50%;
            transform: translate(-50%, -50%);
        }
        .items1{
            width: 100%;
        }
        .item1{
            width: 100%;
            box-shadow: none;
            padding:15px;
            border-radius: 7.5px;
            &:first-child{
                margin:15px 0;
            }
            &:nth-child(2){
                margin:15px 0;
            }
            &:last-child{
                margin: 15px 0;
            }
        }
        .id{
            width: 50px;
            height: 50px;
            margin-right: 15px;
            font-size:22.5px;
            padding-bottom: 5px;
        }
        .block{
            h6{
                font-size: 15px;
            }
            p{
                font-size: 15px;
            }
        }
    }
}


@-webkit-keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}
@keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}

.dark-web-monitoring-page .features .row1{
    .item1{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .item1 .id{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    .item1:hover .id{
        -webkit-animation-name: hvr-icon-wobble-vertical;
        animation-name: hvr-icon-wobble-vertical;
        animation-delay: 0.15s;
        -webkit-animation-delay: 0.15s;
        -webkit-animation-duration: 1.25s;
        animation-duration: 1.25s;
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}