.dark-web-monitoring-page .features .row2{
    margin:5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
        margin-left: 25px;
    }
    .p{
        color:#555;
        font-size: 18px;
        width: 45%;
        margin:15px 0 60px 25px;
    }
    .items2{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item2{
        width: 27.5%;
        padding: 25px;
        border-radius: 10px;
    }
    h6{
        font-size: 20px;
        width: 72%;
        margin:20px 0;
    }
    .icon{
        height: 55px;
    }
    .item2 p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .dark-web-monitoring-page .features .row2{
        h1{
            font-size: 30px;
            margin-left: 20px;
        }
        .p{
            font-size: 15px;
            width: 55%;
            margin:15px 0 40px 20px;
        }
        .item2{
            width: 29%;
            padding: 20px;
        }
        h6{
            font-size: 16px;
            width: 100%;
            margin:15px 0;
        }
        .icon{
            height: 40px;
        }
        .item2 p{
            font-size: 15px;
        }
    }
}

@media(max-width:1401px){
    .dark-web-monitoring-page .features .row2{
        margin:7.5% 0;
        padding:0 7.5%;
        .item2{
            width: 31%;
        }
    }
}

@media(max-width:767px){
    .dark-web-monitoring-page .features .row2{
        margin:35px 0;
        h1{
            font-size: 20px;
            margin-left: 0px;
        }
        .p{
            width: 100%;
            margin:15px 0;
        }
        .item2{
            width: 350px;
            max-width: 100%;
            margin:10px auto;
        }
        h6{
            font-size: 15px;
        }
        .icon{
            height: 40px;
        }
    }
}

.dark-web-monitoring-page .features .row2{
    .item2{
        position: relative;
        overflow: hidden;
        transition: all 200ms linear;
    }
    .item2:hover {
         transform: translateY(-15px);
      }
    .item2:hover::after {
        content: '';
        display: block;
        width: 75px;
        height: 175%;
        background: rgb(255,255,255);
        background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 75%, rgba(255,255,255,0) 100%);
        opacity: 0.5;
        position: absolute;
        top: -50px;
        left: 0;
        animation: shine 500ms linear;
        transform: translateX(1000px) rotate(-25deg);
    }   
}
@keyframes shine {
    0% {
        transform: translateX(-30px) rotate(-25deg);
    }
        
    100% {
        transform: translateX(250px) rotate(-25deg);
    }
}