.dark-web-monitoring-page{
    overflow: hidden;
    .shared-banner{
        background-image: url('../../../../assets/DarkWebMonitoring/banner.png') !important;
        background-attachment: unset;
        padding:12.5% 10% 15%;
        position: relative;
        h1{
            margin:40px 0;
        }
    }
}
.dark-web-monitoring-page .shared-banner .percentage{
    position: absolute;
    width: 16%;
    right: 15%;
    bottom:0;
    .inner{
        width: 100%;
        position: relative;
        transition: 0.3s ease-in-out;
    }
    .img-cont{
        content: url('../../../../assets/DarkWebMonitoring/percentage.png');
        width: 100%;
    }
    .info{
        width: 100%;
        position:absolute;
        color:#111;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        width: 78%;
        height: 78%;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h6{
            font-size: 60px;
            margin-bottom: 10px;
        }
        span{
            font-size: 18px;
        }
    }
    &:hover{
        h6{
            color:#F83030;
        }
        .inner{
            transform: scale(1.035);
        }
    }
}

@media(max-width:1401px){
    .dark-web-monitoring-page{
        .shared-banner{
            h1{
                margin:25px 0;
            }
        }
    }
    .dark-web-monitoring-page .shared-banner .percentage{
        width: 16.5%;
        .info{
            h6{
                font-size: 50px;
                margin-bottom: 7.5px;
            }
            span{
                font-size: 15px;
            }
        }
    }
}

@media(max-width:991px){
    .dark-web-monitoring-page .shared-banner .percentage{
        width: 25%;
    }
}

@media(max-width:767px){
    .dark-web-monitoring-page{
        .shared-banner{
            h1{
                margin:15px 0;
            }
        }
    }
    .dark-web-monitoring-page{
        .shared-banner{
            padding:125px 7.5% 10px;
        }
    }
    .dark-web-monitoring-page .shared-banner .percentage{
        position: relative;
        width: 150px;
        max-width: 100%;
        right: unset;
        bottom:unset;
        margin:50px auto 0;
        .info{
            h6{
                font-size: 40px;
                margin-bottom: 5px;
            }
        }
    }
}